 có thực. Ông có thực cũng như tình yêu và lòng quảng đại luôn hiện diện quanh ta, nhờ đó mà cuộc sống của chúng ta được vui tươi và hạnh phúc. Nếu không có ông già Noel thì thế giới của chúng ta ảm đạm biết bao. Nếu không có những em bé như cháu thì thế giới của chúng ta sẽ như thế nào? Khi đó cuộc sống sẽ chẳng có những tâm hồn trẻ thơ, chẳng có thi ca, chẳng có lãng mạn. Con người chỉ là những cỗ máy khô khan. Ánh sáng niềm tin và hy vọng của trẻ em trên khắp thế giới cũng sẽ tiêu tan. Biên tập viên Francis Pharcellus đã viết một bức thư trả lời cô bé, đồng thời đăng câu chuyện này lên số báo ngày 21/9/1897. Nội dung bức thư trả lời:“Virginia, các bạn cháu nói không đúng. Những người như họ luôn hoài nghi mọi thứ. Họ chỉ tin vào những gì họ tận mắt nhìn thấy và hiểu được, mặc dù trí óc của họ nhỏ bé biết bao. Virginia à, trí tuệ của con người, dù là người lớn hay trẻ em, tất cả đều nhỏ bé. Trong vũ trụ vĩ đại mà chúng ta đang sống, nếu ta hình dung chân lý rộng lớn như là một không gian bao la thì kiến thức của con người chỉ nhỏ nhoi bằng một con kiến mà thôi. "),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_c('br'),_vm._v(" Nếu không tin có ông già Noel, cháu cũng sẽ chẳng tin vào những chuyện thần tiên. Cháu có thể nói với bố nhờ người canh cửa trong đêm Giáng sinh để bắt gặp bằng được ông già Noel, nhưng ngay cả nếu như không gặp được ông đi chăng nữa thì cũng đâu chứng minh được điều gì? Chưa ai tận mắt gặp ông già Noel bằng xương bằng thịt cả nhưng điều đó không có nghĩa là ông già Noel không có thực. Những điều chân thực nhất trong thế giới chúng ta là những điều mà trẻ em và người lớn đều không thể thấy được. Cháu có bao giờ nhìn thấy các nàng tiên nhảy múa trên thảm cỏ chưa? Dĩ nhiên có thể là chưa nhưng đó đâu phải là bằng chứng để cho rằng không có chuyện đó. "),_c('br'),_vm._v(" Không ai có thể hiểu và tưởng tượng 